@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: "Avenir-Heavy";
  src: url(./fonts/Avenir-Heavy.ttf);
  font-weight: 800;
  font-style: normal;
}

* {
  font-family: Avenir, "Avenir-Heavy", sans-serif;
  box-sizing: border-box;
  font-weight: 300;
}

.search__field {
  position: relative;
  margin-top: 20px;
  display: block;
}

.input {
  width: 100%;
  height: 70px;
  font-size: 16px;
  padding-left: 62px;
  line-height: 22px;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  color: #3d4e61;
  outline: none;
}

.search__icon {
  position: absolute;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
  border: 1px solid #e1e7ed;
}

.input:focus {
  line-height: 22px;
  font-size: 16px;
  font-family: "Avenir-Heavy", sans-serif;
  border: 1px solid #e1e7ed;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
}

.input__small {
  height: 42px;
  font-size: 14px;
  padding-left: 33px;
  line-height: 19px;
  font-weight: 400;
}

.search__icon__small {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.input__small:focus {
  line-height: 22px;
  font-size: 14px;
}
